<template>
  <div class="home">
    <div class="container">
      <h1>Card List</h1>
      <button @click="sortOldFirst = !sortOldFirst" class="sort">{{ sortOldFirst ? 'Recent first' : 'Old first' }}</button>
    </div>
        
    <ul class="card-list">      
      <li class="card"><button class="add-btn" @click="$router.push('/create')"></button></li>
      <Card @like-card="likeToggle"
            @edit-card="editCard"
            class="card"
            v-for="card in cardList"
            :key="card.id"
            v-bind:cardData="card" />
    </ul>
  </div>
</template>

<script>
import Card from '../components/Card'
export default {
  props: ['editedCard', 'deletedCard', 'newCard'],
  name: 'Home',
  data() {
    return {
      cardList: [],
      sortOldFirst: false
    }
  },
  components: {
    Card
  },
  methods: {
    sortCards() {
      if (this.sortOldFirst) {
        this.cardList.sort((a, b) => a.id - b.id)
      } else {
        this.cardList.sort((a, b) => b.id - a.id)
      }
    },
    addCard(card) {
      this.cardList.unshift(card)
    },
    saveToStorage() {
      localStorage.setItem('cardList', JSON.stringify(this.cardList))
    },
    likeToggle(id) {
      const index = this.cardList.findIndex(card => card.id === id)

      this.cardList[index].liked = !this.cardList[index].liked
      this.saveToStorage()
    },
    editCard(id) {
      const card = this.cardList.filter(card => card.id === id)[0]

      this.$router.push({ name: 'Edit', params: { id: id, card: card }})
    },
    init() {
      if (localStorage.cardList) {
        this.cardList = JSON.parse(localStorage.cardList)
      }

      if (this.deletedCard) {
        const id = this.deletedCard.id

        setTimeout(() => {
          this.cardList = this.cardList.filter(card => card.id !== id)
        }, 300)      
      }

      if (this.editedCard) {
        const id = this.editedCard.id
        const elemToReplace = this.cardList.filter(card => card.id === id)[0]
        const index = this.cardList.indexOf(elemToReplace)

        setTimeout(() => {
          this.cardList.splice(index, 1, this.editedCard)
        }, 300)      
      }

      if (this.newCard) {
        setTimeout(() => {
          this.addCard(this.newCard)
        }, 300)
      }

      this.sortCards()
    }
  },
  mounted() {
    this.init()
  },
  watch: {
    cardList: function() {
      this.saveToStorage()
    },
    sortOldFirst: function() {
      this.sortCards()
    }
  }
}
</script>

